<template>
  <div class="upload">
    <div class="pageTitle">
      <span class="xqspsc">  </span>
    </div>
    <el-form ref="formObj" :model="formObj" label-width="100px">
      <el-form-item label="">
        <el-radio-group v-model="ActIds">
          <el-radio v-model="ActIds" label="41"></el-radio>
          <el-radio v-model="ActIds" label="43"></el-radio>
          <el-radio v-model="ActIds" label="42"></el-radio>
          <el-radio v-model="ActIds" label="xsd"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="">
        <el-upload class="avatar-uploader" action="#" :before-upload="beforeAvatarUpload">
          <img v-if="formObj.skuPic" :src="formObj.skuPic" class="avatar ztimg">
          <i
            v-else
            class="el-icon-plus avatar-uploader-icon addimg"
            v-loading="loading1"
            style="width: 100%"
          ></i>
        </el-upload>
      </el-form-item>
      <hr>
      <el-form-item label="">
        <el-upload class="avatar-uploader" action="#" :before-upload="beforeAvatarUpload3">
          <img v-for="item in SkuPics" :key="item" :src="item" class="avatar ztimg">
          <i
            class="el-icon-plus avatar-uploader-icon addimg"
            v-loading="loading3"
            style="width: 100%"
          ></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="" prop="skuName">
        <el-input v-model="formObj.skuName"></el-input>
      </el-form-item>
      <el-form-item label="ID" required>
        <el-col :span="10">
          <el-form-item prop="Cateid">
            <el-select v-model="Cateid" placeholder="ID">
              <el-option
                v-for="item in Category"
                :key="item.id"
                :label="item.categoryName"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col class="line" :span="4">ID</el-col>
        <el-col :span="10">
          <el-form-item prop="categoryId">
            <el-select v-model="formObj.goodsSpuInVo.categoryId" placeholder="ID">
              <el-option
                v-for="item in Category2"
                :key="item.id"
                :label="item.categoryName"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-form-item>

      <el-form-item label="" required>
        <el-col :span="10">
          <el-form-item prop="sellPrice">
            <el-input
              type="formObj.stockNum"
              v-model.number="formObj.sellPrice"
              autocomplete="off"
              placeholder=""
            ></el-input>
          </el-form-item>
        </el-col>
        <el-col class="line" :span="4"></el-col>
        <el-col :span="10">
          <el-form-item prop="stockNum">
            <el-input v-model="formObj.stockNum"></el-input>
          </el-form-item>
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-input v-model="formObj.miniNum" disabled></el-input>
      </el-form-item>

      <el-form-item label="" required>
        <el-col :span="11">
          <el-form-item prop="marketPrice">
            <el-input v-model="formObj.marketPrice" placeholder=""></el-input>
          </el-form-item>
        </el-col>
        <el-col class="line" :span="2"></el-col>
        <el-col :span="11" v-if="ActIds!='xsd'">
          <el-form-item label="" prop="actAmount">
            <el-input
              v-model="formObj.actGoodsSkuInVos[0].goodsPromotionRules.actAmount"
              placeholder=""
            ></el-input>
          </el-form-item>
        </el-col>
      </el-form-item>

      <el-form-item label="" required>
        <el-col :span="11">
          <el-form-item prop="realWeight">
            <el-input v-model="formObj.realWeight" placeholder="(KG)"></el-input>
          </el-form-item>
        </el-col>
        <el-col class="line" :span="2"></el-col>
        <el-col :span="11">
          <el-form-item label="" prop="unit">
            <el-select v-model="formObj.unit" placeholder="">
              <el-option v-for="item in units" :key="item" :label="item" :value="item"></el-option>
            </el-select>
            <!-- <el-input v-model="formObj.unit" placeholder="//..."></el-input> -->
          </el-form-item>
        </el-col>
      </el-form-item>

      <el-form-item label="" required>
        <el-col :span="11">
          <el-form-item prop="piece">
            <el-input v-model="formObj.piece"></el-input>
          </el-form-item>
        </el-col>
        <el-col class="line" :span="2"></el-col>
        <el-col :span="11">
          <el-form-item label="" pro="bulk">
            <el-input v-model="formObj.bulk" placeholder="M3"></el-input>
          </el-form-item>
        </el-col>
      </el-form-item>

      <el-form-item label="">
        <el-radio-group v-model="formObj.deliveryTemplateInVo.isFree">
          <el-radio v-model="formObj.deliveryTemplateInVo.isFree" label="0"></el-radio>
          <el-radio v-model="formObj.deliveryTemplateInVo.isFree" disabled label="1"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="">
        <el-radio-group v-model="formObj.sendType">
          <el-radio v-model="formObj.sendType" label="1" v-if="ActIds!='xsd'"></el-radio>
          <el-radio v-model="formObj.sendType" label="2" v-if="ActIds=='xsd'"></el-radio>
          <el-radio v-model="formObj.sendType" disabled label="0"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="">
        <el-radio-group v-model="formObj.singleType">
          <el-radio v-model="formObj.singleType" label="1"></el-radio>
          <el-radio v-model="formObj.singleType" disabled label="2"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="" v-if="ActIds==42">
        <el-col :span="11">
          <el-input v-model="formObj.actGoodsSkuInVos[0].weight"></el-input>
        </el-col>
        <el-col class="line" :span="2"></el-col>
        <el-col :span="11">
          <el-form-item label="">
            <el-input v-model="formObj.actGoodsSkuInVos[0].ratio"></el-input>
          </el-form-item>
        </el-col>
      </el-form-item>
      <el-form-item label="">
        <el-radio-group v-model="formObj.deliveryTemplateInVo.calculateType">
          <el-radio v-model="formObj.deliveryTemplateInVo.calculateType" label="1"></el-radio>
          <el-radio v-model="formObj.deliveryTemplateInVo.calculateType" disabled label="2"></el-radio>
          <el-radio v-model="formObj.deliveryTemplateInVo.calculateType" disabled label="3"></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="" v-if="ActIds=='xsd'">
        <el-checkbox-group v-model="checkList">
          <el-checkbox
            v-for="item in pointList"
            :label="item.id"
            :key="item.id"
          >{{item.salepointName}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="">
        <el-input class="narrow" disabled v-model="formObj.deliveryTemplateInVo.shippingAddress"></el-input>
      </el-form-item>

      <el-form-item label="" required v-if="formObj.sendType!=2">
        <el-col :span="11">
          <el-select v-model="template_id" placeholder="">
            <el-option
              v-for="item in delivery"
              :key="item.id"
              :label="item.deliveryTemplateOut.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-col>
        <el-col class="line" :span="2"></el-col>
        <el-col :span="11" v-if="template_id==''|| template_id==0">
          <el-form-item label="" required>
            <el-input v-model="formObj.deliveryTemplateInVo.name"></el-input>
          </el-form-item>
        </el-col>
      </el-form-item>

      <el-form-item label="" v-if="template_id==''|| template_id==0">
        <el-col :span="11">
          <el-input v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].regionDesc" disabled></el-input>
        </el-col>
      </el-form-item>

      <el-form-item label="" v-if="template_id==''|| template_id==0" required>
        <el-col :span="11">
          <el-input
            v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].firstPiece"
            placeholder=""
          ></el-input>
        </el-col>
        <el-col class="line" :span="2"></el-col>
        <el-col :span="11">
          <el-form-item label="" required>
            <el-input
              v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].secondPiece"
              placeholder=""
            ></el-input>
          </el-form-item>
        </el-col>
      </el-form-item>

      <el-form-item label="" v-if="template_id==''|| template_id==0" required>
        <el-col :span="11">
          <el-input
            v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].firstWeight"
            placeholder="/(kg)"
          ></el-input>
        </el-col>
        <el-col class="line" :span="2"></el-col>
        <el-col :span="11">
          <el-form-item label="" required>
            <el-input
              v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].secondWeight"
              placeholder="/(kg)"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-form-item>

      <el-form-item label="" v-if="template_id==''|| template_id==0" required>
        <el-col :span="11">
          <el-input
            v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].firstBulk"
            placeholder="/(M3/kg)"
          ></el-input>
        </el-col>
        <el-col class="line" :span="2"></el-col>
        <el-col :span="11">
          <el-form-item label="" required>
            <el-input
              v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].secondBulk"
              placeholder="/(M3/kg)"
            ></el-input>
          </el-form-item>
        </el-col>
      </el-form-item>

      <el-form-item label="" v-if="template_id==''|| template_id==0" required>
        <el-col :span="11">
          <el-input
            v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].firstAmount"
            placeholder=""
          ></el-input>
        </el-col>
        <el-col class="line" :span="2"></el-col>
        <el-col :span="11">
          <el-form-item label="" required>
            <el-input
              v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].secondAmount"
              placeholder=""
            ></el-input>
          </el-form-item>
        </el-col>
      </el-form-item>

      <el-form-item label="" v-if="template_id==''|| template_id==0">
        <el-radio-group
          v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].deliveryMethodType"
        >
          <el-radio
            v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].deliveryMethodType"
            label="3"
          ></el-radio>
          <el-radio
            v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].deliveryMethodType"
            disabled
            label="1"
          ></el-radio>
          <el-radio
            v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].deliveryMethodType"
            disabled
            label="2"
          ></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="" v-if="template_id==''|| template_id==0">
        <el-radio-group v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].isDefaultt">
          <el-radio v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].isDefaultt" label="0"></el-radio>
          <el-radio
            v-model="formObj.deliveryTemplateInVo.deliveryMethods[0].isDefaultt"
            disabled
            label="1"
          ></el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item label="" v-if="ActIds==43">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="" v-model="date1" style="width: 100%;"></el-date-picker>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-time-picker
            type="fixed-time"
            placeholder=""
            v-model="date11"
            style="width: 100%;"
          ></el-time-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="" v-if="ActIds==43">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="" v-model="date2" style="width: 100%;"></el-date-picker>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-time-picker
            type="fixed-time"
            placeholder=""
            v-model="date22"
            style="width: 100%;"
          ></el-time-picker>
        </el-col>
      </el-form-item>

      <el-form-item label="" v-if="ActIds==41 ||ActIds==43">
        <el-input class="narrow" v-model="formObj.actGoodsSkuInVos[0].peopleNum"></el-input>
      </el-form-item>

      <el-form-item label="" v-if="ActIds==41 ||ActIds==43">
        <el-input
          type="textarea"
          v-model="formObj.actGoodsSkuInVos[0].ruleDesc"
          placeholder=""
        ></el-input>
      </el-form-item>

      <el-form-item label="">
        <el-input
          type="textarea"
          v-model="formObj.goodsSpuInVo.goodsSpuDesc.title"
          placeholder=""
        ></el-input>
        <div v-for="(item,index) in picUrl" :key="item" class="picimgs">
          <img :src="item" alt="">
          <el-button @click="delpicimg(index)"></el-button>
        </div>
        <el-upload class="avatar-uploader" action="#" :before-upload="beforeAvatarUpload2">
          <i
            class="el-icon-plus avatar-uploader-icon addimg"
            v-loading="loading2"
            :data="picUrl"
            style="width: 100%"
          ></i>
        </el-upload>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
        <el-button @click="Refresh"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>


<script>
import qs from "qs";
export default {
  data() {
    return {
      checkList: [],
      pointList: [], //
      picUrl: [], //
      ActIds: "xsd", //ID  41  43
      Category: [], //
      Cateid: "", //ID
      Category2: [],
      SkuPics: [], //
      Cateid2: "", //ID
      delivery: [], //
      date1: "", // 
      date11: "", // 
      date2: "", //
      date22: "", // 
      template_id: "", //ID
      loading2: false,
      loading1: false,
      loading3: false,
      units: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      form: {
        age: "",
        name: "",
        delivery: false,
        type: [],
        ActId: ""
      },
      formObj: {
        goodsSkuPics: "", //
        skuName: "",
        skuPic: "",
        smallSkuPic: "",
        sellPrice: "",
        marketPrice: "",
        otherMarkerPrice: "",
        costPrice: "",
        stockNum: "9999",
        shopId: "0",
        miniNum: "1",
        sendType: "",
        singleType: "1",
        expiryDate: "90",
        realWeight: "1",
        unit: "",
        piece: "1",
        bulk: "1",
        goodsSkuSpecValue: {
          specValueId: "23"
        },
        goodsSpuInVo: {
          spuName: "",
          spuType: "10",
          categoryId: "",
          brandId: "1",
          goodsSpuSpec: {
            specId: "7"
          },
          goodsSpuDesc: {
            title: "",
            content: ""
          }
        },
        salePointInVo: {
          shopId: "0", //id
          salePointIds: [] //id
        },
        actGoodsSkuInVos: [
          {
            actId: "41",
            shopId: "0",
            categoryId: "",
            peopleNum: "4",
            ruleDesc: "",
            weight: "",
            ratio: "",
            stockNum: "9999",
            startTime: "",
            dueTime: "",
            goodsPromotionRules: {
              ruleType: "4",
              ruleDesc: "",
              shopId: "0",
              actAmount: ""
            }
          }
        ],
        deliveryTemplateInVo: {
          name: "",
          id: "",
          shippingAddress: "",
          isFree: "0",
          calculateType: "1",
          shopId: "0",
          deliveryMethods: [
            {
              regionDesc: "",
              firstPiece: "",
              firstWeight: "",
              firstBulk: "",
              firstAmount: "",
              secondPiece: "",
              secondWeight: "",
              secondBulk: "",
              secondAmount: "",
              deliveryMethodType: "3",
              isDefaultt: "0"
            }
          ]
        }
      },
      rules: {
        skuName: [
          { required: true, message: "", trigger: "blur" }
        ],
        Cateid: [
          { required: true, message: "", trigger: "change" }
        ],
        categoryId: [
          { required: true, message: "", trigger: "change" }
        ],
        sellPrice: [
          { required: true, message: "", trigger: "blur" }
        ],
        stockNum: [
          { required: true, message: "", trigger: "blur" }
        ],
        marketPrice: [
          { required: true, message: "", trigger: "blur" }
        ],
        actAmount: [
          { required: true, message: "", trigger: "blur" }
        ],
        realWeight: [
          { required: true, message: "", trigger: "blur" }
        ],
        unit: [{ required: true, message: "", trigger: "blur" }],
        piece: [{ required: true, message: "", trigger: "blur" }],
        bulk: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  watch: {
    checkList: function() {
      console.log("checkList:", this.checkList);
    },
    ActIds: function() {
      this.formObj.actGoodsSkuInVos[0].actId = this.ActIds;
      if (this.ActIds == 41) {
        this.formObj.sendType = "1";
        this.formObj.actGoodsSkuInVos[0].goodsPromotionRules.ruleDesc =
          "";
        this.formObj.actGoodsSkuInVos[0].goodsPromotionRules.ruleType = "4";
      } else if (this.ActIds == 43) {
        this.formObj.sendType = "1";
        this.formObj.actGoodsSkuInVos[0].goodsPromotionRules.ruleDesc =
          "";
        this.formObj.actGoodsSkuInVos[0].goodsPromotionRules.ruleType = "7";
      } else if (this.ActIds == "xsd") {
        this.getsalePoint();
        this.formObj.sendType = "2";
      } else {
        this.formObj.sendType = "1";
      }
    },
    Cateid: function() {
      let _datas = this.Category;
      this.Category2 = [];
      this.formObj.goodsSpuInVo.categoryId = "";
      for (let i in _datas) {
        if (
          _datas[i].status == 1 ||
          _datas[i].status == 3 ||
          _datas[i].status == 5
        ) {
          if (this.Cateid == _datas[i].id) {
            this.Category2 = _datas[i].children;
          }
        }
      }
    },
    template_id: function() {
      (this.date1 = ""),
        (this.date11 = ""),
        (this.date2 = ""),
        (this.date22 = "");
      for (let i in this.delivery) {
        if (this.template_id == this.delivery[i].id) {
          this.formObj.deliveryTemplateInVo.shippingAddress = this.delivery[
            i
          ].deliveryTemplateOut.shippingAddress;
          if (this.template_id != 0) {
            this.formObj.deliveryTemplateInVo.id = this.template_id;
            this.formObj.deliveryTemplateInVo.name = this.delivery[
              i
            ].deliveryTemplateOut.name;
          } else {
            this.formObj.deliveryTemplateInVo.name = "";
            this.formObj.deliveryTemplateInVo.id = "";
          }
        }
      }
    }
  },
  methods: {
    //
    login() {
      let _this = this;
      _this.deliveryTemplateShop();
      _this.goodsCategory();
      if (this.ActIds == "xsd") {
        this.getsalePoint();
        this.formObj.sendType = "2";
      } else {
        this.formObj.sendType = "1";
      }
    },
    //
    onSubmit() {
      if (!this.formObj.goodsSkuPics) {
        this.$message.error("");
        return;
      }
      if (!this.formObj.skuPic) {
        this.$message.error("");
        return;
      }
      if (this.template_id == "" || this.template_id == 0) {
        if (!this.formObj.deliveryTemplateInVo.name) {
          this.$message.error("");
          return;
        }
      }
      if (!this.formObj.marketPrice) {
        this.$message.error("");
        return;
      }
      if (!this.formObj.goodsSpuInVo.categoryId) {
        this.$message.error("");
        return;
      }
      if (this.formObj.goodsSkuPics.substr(0, 1) == ",") {
        this.formObj.goodsSkuPics = this.formObj.goodsSkuPics.substr(1);
      }
      if (this.ActIds != "xsd") {
        if (this.formObj.marketPrice <= this.formObj.sellPrice) {
          this.$alert("", "", {
            confirmButtonText: "",
            type: "warning",
            callback: action => {}
          });
          return;
        }
        if (
          this.formObj.sellPrice <=
          this.formObj.actGoodsSkuInVos[0].goodsPromotionRules.actAmount
        ) {
          this.$alert("", "", {
            confirmButtonText: "",
            type: "warning",
            callback: action => {}
          });
          return;
        }
      }
      this.formObj.costPrice = this.formObj.marketPrice;
      this.formObj.otherMarkerPrice = this.formObj.marketPrice;
      this.formObj.goodsSpuInVo.spuName = this.formObj.skuName;
      this.formObj.goodsSpuInVo.categoryId = this.formObj.goodsSpuInVo.categoryId;

      if (this.formObj.deliveryTemplateInVo.deliveryMethods[0].firstWeight) {
        delete this.formObj.deliveryTemplateInVo.id;
      }
      if (this.formObj.actGoodsSkuInVos) {
        if (!this.formObj.actGoodsSkuInVos[0].ruleDesc) {
          delete this.formObj.actGoodsSkuInVos[0].ruleDesc;
        }
      }

      if (this.ActIds == 43) {
        if (new Date(this.date11).getTime() > new Date(this.date22).getTime()) {
          this.open();
          return;
        }
        this.date1 = this.formatDate(this.date1, 1);
        this.date11 = this.formatDate(this.date11, 2);
        this.date2 = this.formatDate(this.date2, 1);
        this.date22 = this.formatDate(this.date22, 2);
        this.formObj.actGoodsSkuInVos[0].startTime = this.date1 + this.date11;
        this.formObj.actGoodsSkuInVos[0].dueTime = this.date2 + this.date22;
        this.formObj.actGoodsSkuInVos[0].categoryId = this.formObj.goodsSpuInVo.categoryId;
      } else if (this.ActIds == "xsd") {
        if (this.checkList.length < 1) {
          this.$alert("", "", {
            confirmButtonText: "",
            type: "warning",
            callback: action => {}
          });
          return;
        } else {
          this.formObj.salePointInVo.salePointIds = this.checkList;
        }
        delete this.formObj.actGoodsSkuInVos;
      } else if (this.ActIds == 42) {
        delete this.formObj.actGoodsSkuInVos[0].goodsPromotionRules;
        delete this.formObj.actGoodsSkuInVos[0].peopleNum;
        delete this.formObj.actGoodsSkuInVos[0].ruleDesc;
        delete this.formObj.actGoodsSkuInVos[0].startTime;
        delete this.formObj.actGoodsSkuInVos[0].dueTime;
      } else {
        this.formObj.actGoodsSkuInVos[0].categoryId = this.formObj.goodsSpuInVo.categoryId;
      }
      if (this.formObj.sendType != 2) {
        _this.template_id = "";
        _this.formObj.deliveryTemplateInVo.shippingAddress = "";
      }

      let _content = "",
        _picUrl = "";
      _content += "<p>" + this.formObj.goodsSpuInVo.goodsSpuDesc.title + "</p>";
      if (this.picUrl) {
        for (let i in this.picUrl) {
          let _img = '<img src="' + this.picUrl[i] + '">';
          _picUrl += _img;
        }
      }
      _content += _picUrl;
      this.formObj.goodsSpuInVo.goodsSpuDesc.content = _content;
      const _formObj = JSON.stringify(this.formObj);
      console.log("_formObj:", _formObj);
      let _Url = "";
      if (this.ActIds == "xsd") {
        delete this.formObj.actGoodsSkuInVos;
        _Url = this.$GLOBAL.API+"app/goodsSku/addPlatformGoodsSkuAndSalePoint";
      } else {
        delete this.formObj.salePointInVo;
        _Url = this.$GLOBAL.API+"app/goodsSku/addPlatformGoodsSkuAndAct";
      }
      this.$axios({
        method: "post",
        url: _Url,
        data: this.formObj,
        headers: {
          Authorization: sessionStorage.getItem("_TOKEN"),
          "Content-Type": "application/json"
        }
      }).then(res => {
        if (res.data.code == 0) {
          window.scrollTo(0, 0);
          this.open2();
        }
      });
    },
    //
    open2() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "success"
      })
        .then(() => {
          this.Refresh();
        })
        .catch(() => {});
    },
    //
    getsalePoint() {
      let dataObj = {},
        _value = "",
        _this = this;
      dataObj = {
        page: 1,
        rows: 50,
        city: "",
        type: 1
      };
      for (let key in dataObj) {
        _value += key + "=" + dataObj[key] + "&";
      }
      _value = _value.substring(0, _value.length - 1);
      let _Url = this.$GLOBAL.API+"app/salePoint/listNew?" + _value;
      _Url = encodeURI(_Url);
      this.$axios({
        url: _Url,
        method: "get",
        headers: {
          Authorization: sessionStorage.getItem("_TOKEN")
        }
      }).then(res => {
        if (res.data.code == 0) {
          _this.pointList = res.data.data.list;
          window.scrollTo(0, 0);
        }
      });
    },
    //
    Refresh() {
      history.go(0);
    },
    //
    beforeAvatarUpload2(file) {
      this.loading2 = true;
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 1;
      if (!isLt2M) {
        this.$message.error(" 500kb!");
      } else {
        let form = new FormData(),
          _this = this;
        form.append("file", file);
        form.append("userName", "13971489895");
        this.$axios
          .post(this.$GLOBAL.API+"img/uploadMp4", form, {
            headers: {
              Authorization: sessionStorage.getItem("_TOKEN")
            }
          })
          .then(res => {
            if (res.data.code == 0) {
              if (res.data.data.picUrl) {
                _this.loading2 = false;
                _this.picUrl.push(res.data.data.picUrl);
              }
            }
          });
      }
    },
    //1
    beforeAvatarUpload(file) {
      this.loading1 = true;
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 1;
      if (!isLt2M) {
        this.$message.error(" 500kb!");
      } else {
        let form = new FormData(),
          _this = this;
        form.append("file", file);
        form.append("userName", "13971489895");
        this.$axios
          .post(this.$GLOBAL.API+"img/uploadMp4", form, {
            headers: {
              Authorization: sessionStorage.getItem("_TOKEN")
            }
          })
          .then(res => {
            if (res.data.code == 0) {
              if (res.data.data.picUrl) {
                _this.loading1 = false;
                _this.formObj.skuPic = res.data.data.picUrl;
                _this.formObj.smallSkuPic = res.data.data.smallPicUrl;
              }
            }
          });
      }
    },
    //3
    beforeAvatarUpload3(file) {
      if (this.SkuPics.length > 4) {
        this.$message.error("");
        return;
      }
      this.loading3 = true;
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 1;
      if (!isLt2M) {
        this.$message.error(" 500kb!");
      } else {
        let form = new FormData(),
          _this = this;
        form.append("file", file);
        form.append("userName", "13971489895");
        this.$axios
          .post(this.$GLOBAL.API+"img/uploadMp4", form, {
            headers: {
              Authorization: essionStsorage.getItem("_TOKEN")
            }
          })
          .then(res => {
            if (res.data.code == 0) {
              _this.loading3 = false;
              let _data = res.data.data;
              _this.formObj.goodsSkuPics =
                _this.formObj.goodsSkuPics + "," + _data.id;
              _this.SkuPics.push(_data.picUrl);
            }
          });
      }
    },
    //
    delpicimg(ind) {
      this.picUrl.splice(ind, 1);
    },
    //
    deliveryTemplateShop() {
      let _this = this;
      this.$axios
        .get(this.$GLOBAL.API+"app/deliveryTemplateShop/list?shopId=0&page=1&rows=100", {
          headers: {
            Authorization: sessionStorage.getItem("_TOKEN")
          }
        })
        .then(res => {
          if (res.data.code == 0) {
            if (res.data.data.list && res.data.data.list.length > 0) {
              _this.delivery = res.data.data.list;
              let obj2 = {
                deliveryTemplateOut: {
                  name: ""
                },
                id: 0
              };
              obj2.id = 0;
              obj2.deliveryTemplateOut.name = "";
              _this.delivery.push(obj2);
              for (let i in _this.delivery) {
                if (
                  _this.delivery[i].deliveryTemplateOut.name ==
                  "8"
                ) {
                  _this.template_id = _this.delivery[i].id;
                  _this.formObj.deliveryTemplateInVo.shippingAddress =
                    _this.delivery[i].deliveryTemplateOut.shippingAddress;
                }
              }
            }
          }
        });
    },
    // 
    goodsCategory() {
      this.$axios
        .get(this.$GLOBAL.API+"goodsCategory/list", {
          headers: {
            Authorization: sessionStorage.getItem("_TOKEN")
          }
        })
        .then(res => {
          if (res.data.code == 0) {
            let _data = res.data.data,
              arr = [];
            for (let i in _data) {
              if (
                _data[i].status == 1 ||
                _data[i].status == 3 ||
                _data[i].status == 5
              ) {
                this.Category.push(_data[i]);
              }
            }
          }
        });
    },
    //
    formatDate(date, val) {
      if (date) {
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let day = date.getDate();
        let hour = date.getHours();
        let minute = date.getMinutes();
        let second = date.getSeconds();
        if (val == 1) {
          return (
            year + "-" + this.formatTen(month) + "-" + this.formatTen(day) + " "
          );
        } else if (val == 2) {
          return hour + ":" + minute + ":" + second;
        }
      }
    },
    formatTen(num) {
      return num > 9 ? num + "" : "0" + num;
    },
    open() {
      this.$alert("", "", {
        confirmButtonText: "",
        callback: action => {}
      });
    }
  },
  created() {
    this.login();
  }
};
</script>

