<script>
var E = require("wangeditor");
export default {
  name: "xiangqiversion",
  data() {
    return {
      baseUrl: "https://xiang7.net/",
      tonav: [
        {
          name: "",
          target: "#banner"
        },
        {
          name: " ",
          target: "#redbag"
        },
        {
          name: "",
          target: "#navs"
        },
        {
          name: "",
          target: "#activity"
        },
        {
          name: "",
          target: "#subswiper"
        },

        {
          name: "",
          target: "#hengfu"
        },
        {
          name: "",
          target: "#zone"
        },
        {
          name: "",
          target: "#indexShare"
        },
        {
          name: "",
          target: "#citylist"
        },
        {
          name: "",
          target: "#express"
        },
        {
          name: "",
          target: "#withdraw"
        },
        {
          name: "",
          target: "#videoShow"
        },
        {
          name: "",
          target: "#regulation"
        }
      ],
      editorContent: "",
      subswiper: [], //
      bannerList: [], //
      navsList: [], //
      navbannerData: {}, //
      activityList: [], //
      showRebbag: true, //1
      showRebbag2: true, //2
      fresh0: {}, // 
      zoneList: [], // 3
      indexShare: {}, // 
      cityList: [], //
      express: {}, //
      withdraw: {}, //
      oldData: {},
      regulation: {
        pintuan: {
          listdesc: "",
          detaildesc: "",
          orderdesc: ""
        },
        checkin: "",
        lotteryredbag: "",
        bargain: {
          goods: "",
          dish: ""
        },
        redbagdesc: "",
        voucher: "",
        voucherRule:{
          xiang7:'',
          kanjia:'',
          miaosha:'',
          pintuan:'',
          xianggou:''
        }
      }, //
      isScroll: false,
      videoShow: false
    };
  },
  created() {
    this.getData();
    console.log("created");
  },
  mounted() {
    var editor = new E("#editorElem");
    editor.customConfig.onchange = html => {
      this.editorContent = html;
    };
    editor.create();
    this.$prompt("", "", {
      confirmButtonText: "",
      showClose: false,
      showCancelButton: false,
      closeOnClickModal: false,
      closeOnPressEscape: false,
      closeOnHashChange: false,
      inputValidator: function(value) {
        return value == "2018" ? true : false;
      }
    })
      .then(({ value }) => {
        this.$message({
          type: "success",
          message: " "
        });
        window.scrollTo(0,0);
      })
      .catch(() => {});
  },

  methods: {
    selectdomain(e) {
      this.$GLOBAL.API = e;
      this.login("check", e);
      console.log(this.$GLOBAL.API)
    },
    login(type, e) {
      let _this = this;
      // this.$GLOBAL.API+"  <==>  this.$GLOBAL.API+"
      this.$axios
        .post(this.$GLOBAL.API + "webApprove/auth/login?userName=13971489895")
        .then(res => {
          _this.token = "Bearer " + res.data.data;
          let _token = "Bearer " + res.data.data;
          sessionStorage.setItem("_TOKEN", _token);
          this.getData(type, e);
        })
        .catch(err => {
          console.log(err);
          
        });
    },
    getContent: function() {
      alert(this.editorContent);
    },
    addKey(_key, _value, _desc, _type, id) {
      var params = new URLSearchParams();
      params.append("configKey", _key);
      params.append("configDesc", _desc);
      params.append("type", "1");
      if (_value instanceof Array || _value instanceof Object) {
        params.append("configValue", JSON.stringify(_value));
      } else {
        params.append("configValue", _value);
      }
      if (id) {
        params.append("id", id);
      }
      this.$axios
        .post(this.$GLOBAL.API + "globalConfig/add", params, {
          headers: {
            Authorization: sessionStorage.getItem("_TOKEN")
          }
        })
        .then(res => {
          var msg = id ? "" : "";
          this.$message({
            message: msg,
            type: "success"
          });
          this.getData();
        });
    },
    deleteCongig(id) {
      var params = new URLSearchParams();
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(({ value }) => {
          if (value != "15927505686") {
            this.$message.error("");
          } else {
            params.append("id", id);
            this.$axios
              .post(this.$GLOBAL.API + "globalConfig/delete", params, {
                headers: {
                  Authorization: sessionStorage.getItem("_TOKEN")
                }
              })
              .then(res => {
                this.$message({
                  type: "success",
                  message: ": "
                });
              });
          }
        })
        .catch(() => {});
    },
    getData(type, e) {
      this.$axios
        .get(this.$GLOBAL.API + "globalConfig/list?type=1", {
          headers: {
            Authorization: sessionStorage.getItem("_TOKEN")
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code == 0) {
            this.oldData = res.data.data;
            try {
              for (var k in this.oldData) {
                this.oldData[k].configValue = JSON.parse(
                  this.oldData[k].configValue
                );
              }
              for (let key in this.oldData) {
                for (let ind in this) {
                  if (key == ind) {
                    this[ind] = this.oldData[key].configValue;
                  }
                }
              }
              if (type == "check") {
                this.$notify({
                  title: "",
                  message:
                    "" + (e == "/api" ? "" : "") + "",
                  type: "success"
                });
              }
            } catch (err) {}
          }
        });
    },
    itemsortleft(index, keyName) {
      if (index == 0) {
        return false;
      }
      for (var k in this) {
        if (k == keyName) {
          this[k].splice(
            index,
            1,
            ...this[k].splice(index - 1, 1, this[k][index])
          );
        }
      }
    },
    itemsortright(index, keyName) {
      for (var k in this) {
        if (k == keyName) {
          if (index >= this[k].length - 1) {
            return false;
          }
          this[k].splice(
            index,
            1,
            ...this[k].splice(index + 1, 1, this[k][index])
          );
        }
      }
    },
    deleteItem(index, keyName) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          for (var k in this) {
            if (k == keyName) {
              console.log(this[k]);
              this[k].splice(index, 1);
            }
          }
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {});
    },
    deleteItemRedbag() {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.navbannerData = {};
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {});
    },
    deletehengfuitem() {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.fresh0 = {};
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {});
    },
    deletindexShare() {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.indexShare.imgUrl = "";
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {});
    },
    beforeAvatarUpload(file) {
      let that = this;
      uploadImg(file, this).then(res => {
        let obj = {
          imgUrl: res,
          isShow: true,
          isLink: true,
          tips: "",
          linkUrl: ""
        };
        that.bannerList.push(obj);
      });
    },
    beforeAvatarUpload2(file) {
      let that = this;
      uploadImg(file, this).then(res => {
        let obj = {
          imgUrl: res,
          name: "",
          isShow: true,
          isLink: true,
          tips: "",
          linkUrl: ""
        };
        that.navsList.push(obj);
      });
    },
    beforeAvatarUpload3(file) {
      let that = this;
      uploadImg(file, this).then(res => {
        let obj = {
          imgUrl: res,
          isShow: true,
          isLink: true,
          tips: "",
          linkUrl: ""
        };
        that.activityList.push(obj);
      });
    },
    beforeAvatarUpload4(file) {
      let that = this;
      uploadImg(file, this).then(res => {
        that.fresh0 = {
          imgUrl: res,
          isShow: true,
          isLink: true,
          tips: "",
          linkUrl: ""
        };
      });
    },
    beforeAvatarUpload5(file) {
      let that = this;
      uploadImg(file, this).then(res => {
        let obj = {
          imgUrl: res,
          isShow: true,
          isLink: true,
          tips: "",
          linkUrl: ""
        };
        that.zoneList.push(obj);
      });
    },
    beforeAvatarUpload6(file) {
      let that = this;
      uploadImg(file, this).then(res => {
        that.indexShare = {
          imgUrl: res
        };
      });
    },
    beforeAvatarUpload7(file) {
      let that = this;
      uploadImg(file, this).then(res => {
        let obj = {
          imgUrl: res,
          isShow: true,
          name: "",
          isOpen: true,
          tips: ""
        };
        that.cityList.push(obj);
      });
    },
    beforeAvatarUpload8(file) {
      let that = this;
      uploadImg(file, this).then(res => {
        that.navbannerData = {
          imgUrl: res
        };
      });
    },
    beforeAvatarUpload9(file) {
      let that = this;
      uploadImg(file, this).then(res => {
        let obj = {
          imgUrl: res,
          isShow: true,
          isLink: true,
          tips: "",
          linkUrl: ""
        };
        that.subswiper.push(obj);
      });
    }
  }
};

var uploadImg = function(file, _this) {
  return new Promise((resolve, reject) => {
    let form = new FormData();
    let that = _this;
    form.append("file", file);
    form.append("userName", "15927505686");
    that.$axios
      .post(that.$GLOBAL.API + "img/uploadMp4", form, {
        headers: {
          Authorization: sessionStorage.getItem("_TOKEN")
        }
      })
      .then(res => {
        if (res.data.code == 0) {
          return resolve(res.data.data.picUrl);
        }
      });
  });
};

</script> 
<style scoped lang="less">
